import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { EmailMaintenanceComponent } from './email-maintenance.component';
import { EmailSummaryComponent } from './email-summary/email-summary.component';
import { EmailNewComponent } from './email-new/email-new.component';

export const emailRoutes: Routes = [
  {
    path:'',
    component:EmailSummaryComponent
  },
  {
    path:'email-maintenance',
    component:EmailMaintenanceComponent
  },
  {
    path:'email-new',
    component:EmailNewComponent
  }
]
