import { Component, OnInit } from '@angular/core';
import Swal from 'sweetalert2';
import { FormGroup, FormBuilder } from '@angular/forms';
import { AccountBlockingServiceService } from 'src/app/shared/services/account-blocking-service.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-email-new',
  templateUrl: './email-new.component.html',
  styleUrls: ['./email-new.component.css']
})
export class EmailNewComponent implements OnInit {

  accountType: any;
  selected:any;
  dataSource: any;
  loggedInUser:any;
  emailMaintenace: FormGroup;
  emaildata:any;
  masterresponse:any;
  masterDatapermission:boolean;
  showSubmit: boolean;
  constructor(private formBuilder: FormBuilder, private accountBlockingService: AccountBlockingServiceService, private route: ActivatedRoute) {

  }
  
  ngOnInit() {
    this.loggedInUser = localStorage.getItem('userFromLogin');
    
  }

  // emailMaitenanceDto=new EmailMaitenanceDto();
  private fieldArray: Array<any> = [];
  private newAttribute: any = {};

  addFieldValue() {
    this.fieldArray.push(this.newAttribute)
    this.newAttribute = {};

  }

  deleteFieldValue(index) {
    this.fieldArray.splice(index, 1);
  }
  onClickOfSubmit(data: any) {
    //console.log("submit", data)
    //console.log(this.fieldArray);
  }
  public email: any[] = [{
    emailId: '',
    emailType: '',
    emailRequired:'',
    id:'',
  }]
  addAddress() {
    this.email.push({
      emailId: '',

    });
  }
  removeAddress(i: number) {
    this.email.splice(i, 1);
  }
 
  logValue() {
    //console.log(this.selected);
   // //console.log(this.accountType);
    //console.log(this.email);
    this.email.forEach(element => {
      element.emailType=this.selected;
      element.emailRequired='Y';
    });
    this.accountBlockingService.emailMaintenance(this.selected,this.loggedInUser,this.email).subscribe(resp => {
     this.masterresponse=resp;
     //console.log(resp);
      if(resp){
        this.masterDatapermission=true;
       Swal.fire('Success ');
       this.showSubmit=true;
     }
     else if(resp==null)
     {
      // this.masterDatapermission=false;
      Swal.fire('Email Id Already Exit ');
     }
     else{
       Swal.fire('Unable to Save the Record ');
     }
     
    });
  }
 

}



