import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import { AccountBlockingServiceService } from 'src/app/shared/services/account-blocking-service.service';
import { MatTableDataSource, MatPaginator } from '@angular/material';
import { UsersService } from '../../users/users.service';

@Component({
  selector: 'app-email-summary',
  templateUrl: './email-summary.component.html',
  styleUrls: ['./email-summary.component.css']
})
export class EmailSummaryComponent implements OnInit {
  emaildata: any;
  dataSource: any;
  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  displayedColumns: string[] = ['emailType', 'inputBy'];
  user_id:any;
  screenName:any;
  role:any;
  res:any;
  rolepermission:any;
  button_permission:any={};
  constructor(private router: Router, private userApi: UsersService,
    private accountBlockingService: AccountBlockingServiceService) { }

  ngOnInit() {
    this.user_id=sessionStorage.getItem('user_id');
    this.role=sessionStorage.getItem('user_role');
    this.getAllData();
    this.screenpermission();
  }
  screenpermission(){
    this.screenName='roles';
    //this.role='ROLE1';
    this.userApi.getRoleScreenPermission(this.user_id,this.screenName,this.role).subscribe(res=>{
      this.rolepermission=res;
      //console.log(this.rolepermission);
 if(this.rolepermission){
        this.button_permission=this.rolepermission[0];
        //console.log( this.button_permission);
        }
    });
   
  }


  getEmail(row) {
    //console.log("row",row);
    //console.log(row.modNO);
    const navigationExtras: NavigationExtras = {
      queryParams: {
        modifyNo:row.modNO,
        creator:row.inputBy,
        rstatus:row.recordStatus,
        vStatus:row.verifiedOnce,
        inputDate:row.inputTime,
        vDate:row.verifiedStatus,
        authtym:row.verifiedTime,
        authBy:row.verifiedBy,
        emailType:row.emailType
      }

    };
    //console.log(row.emailType);
      this.router.navigate(['email-summary/email-maintenance'],navigationExtras);
  }
  getAllData() {
    this.accountBlockingService.getAllEmail().subscribe(emaildata => {
      this.emaildata = emaildata;
      //console.log(this.emaildata);
      this.dataSource = new MatTableDataSource(this.emaildata);
      this.dataSource.paginator = this.paginator;

    })
    let response: any = JSON.stringify(this.dataSource);
    //console.log(response)
    localStorage.setItem("EmailSwitch", "existing");
    localStorage.setItem("responseFromEmailSummary", response);
  }
}
